<template>
    <div class="container">
        <div class="main__info">
            <h2>Name: {{ getCar.name }}</h2>
            <h3>USERNAME: {{  getCar.username }}</h3>
            <a href="https://www.google.com/">EMAIL: {{ getCar.email }}</a>
            <p>STREET: {{ getCar.street }}</p>
            <a href="#">SUITE: {{ getCar.suite }}</a>
            <p>CITY: {{ getCar.city }}</p>
            <a href="https://www.google.com/maps/d/viewer?mid=1fYwfWErE4gKO8jMkDr-1IeUE_dA&hl=en&ll=35.15803832376823%2C-106.57802226446535&z=15">ZIPCODE: {{ getCar.zipcode }}</a>
            <a href="https://voice.google.com/u/0/about">PHONE: {{ getCar.phone }}</a>
            <p>WEBSITE: {{ getCar.website }}</p>
            <p>COMPANY-NAME: {{ getCar.name }}</p>
            <p>CATCHPHRASE: {{ getCar.catchPhrase }}</p>
            <p>BS: {{ getCar.bs }}</p>  
        </div>
    </div>
</template>

<script>
import { items } from '@/data'
    export default {
        data() {
            return {
                cars: items,
                currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
        },
        computed: {
            getCar() {
                return this.cars.find(car => car.id == this.currentId)
            }
        }
    }
</script>

