<template>
    <div>
        <div class="main__car" v-if="users">
            <div class="main__car-item" v-for="add in users" :key="add.id">
                <p>POSTID: {{ add.postId }}</p>
                <p>ID: {{ add.id }}</p>
                <p>NAME: {{ add.name }}</p>
                <a href="https://account.mail.ru/login?page=https%3A%2F%2Fe.mail.ru%2Finbox%2F&allow_external=1" class="main__car-item-a">EMAIL: {{ add.email }}</a>
                <p class="Contact-p">BODY: {{ add.body }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                users: null
            }
        },
        mounted() {
            this.getUsers()
        },
        methods: {
            async getUsers() {
                let response = await fetch('https://jsonplaceholder.typicode.com/comments')
                let answer = await response.json()
                this.users = answer
            }
        },
    }
</script>

